<template>
<div class="circular-progress">
	<svg 
		xmlns='http://www.w3.org/2000/svg'
		viewBox='0 0 100 100'
		aria-labelledby='title' role='graphic'>
		<circle cx="50" cy="50" r="40" ></circle>
		<circle cx="50" cy="50" r="40" id='pct-ind' v-bind:style="{strokeDashoffset: this.pctIndicator}"></circle>
	</svg>
	<p class="pct">{{percent}}%</p>
	<p class="name">{{name}}</p>
</div>
</template>
<script>

	export default {
		data: function () {
	      return {
	        pctIndicator: ( 1 - this.percent / 100 ) * (2 * (22 / 7) * 40)
	      };
	    },
		props:['percent', 'name']
		}

</script>
<style scoped lang="scss">
$circle-circumstance: ( 2 * (22 / 7) * 40 );


.circular-progress { 
	position: relative;
	display: inline-block;
	width: 200px;
	margin: 40px;
 	border-radius: 50%;
  svg {
    transform: rotate(270deg);
  }
  circle {
    stroke-width: 5;
    fill: none;
		stroke-linecap: round;
  }
  circle:nth-of-type(1) {
    stroke: #dee2e6;
  }
  circle:nth-of-type(2) {
    stroke: #d8503a;
    stroke-dasharray: $circle-circumstance;
		
		// here we multiply the circle circumstance by an opposite number of what we want it to present
		// so here we want it to present 70% which means 0.7 and the opposite of that is 0.3 ( 1 - 0.7 )
    stroke-dashoffset: ( 0.3 * $circle-circumstance );
  }
	
	.name {
		margin: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate( -50% , -50% );
		text-transform: uppercase;
	}
	.pct{
		text-align: center;
	}
}

// the code under below is just to chnage bootstrap css custom range color ( the color of the thumb )
#slider {
	margin: 0 50px;
	width: 50%;
	&::-moz-range-thumb {
		background-color: #d8503a;
	}
	&::-webkit-slider-thumb {
		background-color: #d8503a;
	}
	&::-ms-thumb {
		background-color: #d8503a;
	}
	
	&:focus {
		&::-moz-range-thumb {
			box-shadow: 0 0 0 1px #fff, 0 0 0 0.2rem rgba(#d8503a, 0.25);

		}
		&::-webkit-slider-thumb {
			box-shadow: 0 0 0 1px #fff, 0 0 0 0.2rem rgba(#d8503a, 0.25);
		}
		&::-ms-thumb {
			box-shadow: 0 0 0 1px #fff, 0 0 0 0.2rem rgba(#d8503a, 0.25);
		}
	}
}


</style>